body{

}
.header {
   display: flex;
   align-items: center;
   justify-content: space-between;
   background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
   color: white;
}
.logo1 {
   margin-left: 30px;
   width: 50px;
   height: 40px;
}
.header1 {
   margin-left: -100px;
}
.header_p {
   margin-right: 30px;
}
.meme_container {
   padding: 30px;
}
.meme_container2{
   display: grid;
   grid-template: 40px 40px /1fr 1fr;
   gap: 20px;
   margin-bottom: 20px;
}
.form_button {
   grid-column: 1 / -1;
   border-radius: 5px;
   border: 1 solid black;
   color: white;
   background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
   cursor: pointer;

}
.form_input {
   border-radius: 5px;
   border: 1 solid black;
   text-indent:  5px;
}
.square_box {
   height: 100px;
   width: 100px;
   border: 1px solid black;
   display: inline-block;
   margin-right: 4px;
}
input {
   margin-bottom: 15px;
   height: 30px;
   text-indent: 10px;
   border: 1px solid #444;
   border-radius: 5px;
   display: block;
}
.meme_container3 {
   position: relative;
   text-align: center;
}
.meme_image_container {
   position: relative;
   display: inline-block;
}
.meme_image  {
   max-width: 100%;
   height: auto;
}
.meme_top_text, .meme_bottom_text {
   position: absolute;
   width: 100%;
   text-align: center;
}
.meme_top_text{
   top: 0;
   color: white;
   font-size: 40px;
   text-shadow: 0 0 3px #423d3d, 0 0 3px #895d8d;
}
.meme_bottom_text{
   bottom: 0;
   color: white;
   font-size: 40px;
   text-shadow: 0 0 3px #423d3d, 0 0 3px #8d5d81;
}